main.landingPage {

}

main.landingPage > .content {
    width: 100%;
    /* height: 800px; */
    text-align: center;
    align-content: center;
    justify-content: center;
    opacity:.8;
    border-top: 2px dashed black;
    background: #283c86; /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #283c86, #45a247); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #283c86, #45a247); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    display: flex;
}

main.landingPage > .content > table {
    top: 200px;
    position: relative;
    color: white;
    text-transform: lowercase;
    letter-spacing: 3px;
    font-size: 25px;
    font-weight: 900;
    width:50%;
    padding-bottom: 400px;
}

table > tbody > tr > th, table > thead > tr > th {
    width: 50%;
}

table > tbody > tr > th {
    font-size: 15px;
}